
#WEATHER {
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}
.weather-bg {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.weather-head {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}
.weather-head li {
  display: inline-block;
  padding: 1em 2em;
  color: white;
  font-size: 3em;
  font-weight: bold;
}
.weather-head .weather-data {
  float: left;
}
.weather-head .weather-c {
  float: right;
}
.weather-c p {
  line-height: 1;
  text-align: right;
}
.weather-c .forecast {
  font-size: 4em;
  font-weight: bolder;
}
.weather-c .forecast::after {
  content: 'o';
  position: relative;
  top: -80px;
  font-size: 0.2em;
  font-weight: normal;
}
.weather-data .month::after {
  content: '1';
  display: block;
  font-size: 0px;
  font-weight: 0px;
  position: relative;
  top: 5px;
  left: 8px;
  width: 20px;
  height: 5px;
  background-color: white;
}
.weather-data .day {
  font-weight: bolder;
  font-size: 2em;
}
.weather-info {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 200px;
  position: relative;
  top: -200px;
  font-size: 0px;
  padding-top: 1.5rem;
}
.weather-today, .weather-week {
  display: inline-block;
}
.weather-info .weather-today {
  font-size: 1rem;
  padding-left: 1rem;
}
.weather-info .weather-today p{
  text-align: left;
  font-size: 3em;
  color: #AB9781;
}
.weather-today p.title{
  color: white;
  font-weight: bold;
  font-size: 4em;
}
.weather-info .weather-week {
  width: 80%;
}
.weather-info .weather-week ul {
  font-size: 0px;
  color: white;
}
.weather-week li {
  display: inline-block;
  font-size: 3.5rem;
  width: 20%;
}
.weather-week li p {
  text-align: center;
}
.weather-week li p.icon .material-design-icon {
  fill: white;
}
.weather-week li p.updown span::after {
  content: 'o';
  position: relative;
  top: -20px;
  font-size: 0.2em;
  font-weight: bold;
}



